body{
    margin:0px;
}

body, input, select,textarea,option{
    font-family: sans-serif;
}

section{
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
section>*{
    -webkit-align-self: center;
            align-self: center;
}
.input input{
    width:100%;
    padding:10px;
    border:0px;
    outline:0px;
    border-bottom: 1px solid #999;
   font-size: 20px;
   color:#555;
}
.input label{
    position: absolute;
    padding: 10px;
    font-size: 20px;
    color:#777;
    -webkit-transition: font-size 0.5s,margin-top 0.5s, padding 0.5s;
    transition: font-size 0.5s,margin-top 0.5s, padding 0.5s;
}
.input.focused label{
    font-size: 10px;
    margin-top:-10px;
    padding:0px;
}
.input{
    margin:10px;
}
.button button{
    margin-top: 30px;
    border: 0px;
    background: #ffffff;
    padding: 15px 20px;
    font-size: 20px;
    border-radius: 1px;
    border: 1px solid #ddd;
    outline: none;
    -webkit-transition: background 0.3s;
    transition: background 0.3s;
}
.button button:hover{
    background: #eee;
}
.button{
    text-align: center;
}
.loginBox{
    background: #ffffff;
    padding: 20px 50px;
    box-shadow: 0px 0px 10px #ccc;
}

.loginBox h1{
   text-align: center;
}
header{
    
    box-shadow: 0px 0px 20px #aaa;
   margin-bottom:20px;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: baseline;
           align-items: baseline;
   position: -webkit-sticky;
   position: sticky;
   top:0px;
}
#logo{
   
    background: url(https://inkers.ai/careers/interns/images/logoK@2x.png) no-repeat;
    background-size: 61px 14px;
    margin-left:50px;
   font-size: 20px;
}
#logo a{
    color:transparent;
}

nav ul{
    display: -webkit-inline-flex;
    display: inline-flex;
    margin:0px;
    list-style: none;
}

li a{
    color:#222;
    padding:20px;
    display: inline-block;
    text-decoration: none;
}
.filler{
    display: inline-block;
    padding:20px;
}
nav a:hover,.active{
    background: #eee;
}

.toast{
    position: fixed;
    bottom:-30%;
    background: rgba(0,0,0,0.9);
    color:#eee;
    padding:10px;
    width:100%;
    text-align: center;
    -webkit-transition:bottom 1s ease-in-out;
    transition:bottom 1s ease-in-out;
}
.toast.show{
    bottom: 30%;
}
